<template>
  <div v-if="has" class="c-guide u-font--black u-background--blue is-fill">
    <van-swipe
      class="c-guide__content"
      @change="onBannerChange"
      :loop="false"
    >
      <van-swipe-item
        v-for="(item, index) in picList"
        :key="index"
        class="l-flex--column"
      >
        <div class=c-guide__img>
          <v-resource
            class="is-cover"
            :info="item.picInfo"
          />
        </div>
        <div class="c-guide__text u-text-center u-background--white">
          <div class="is-title--page--blue">{{ step.title }}</div>
          <div class="is-tip--dark u-font u-separate--near" v-html="step.content"></div>
        </div>
      </van-swipe-item>
    </van-swipe>
    <div class="c-guide__footer">
      <div class="c-guide__button">
        <div
          class="o-button is-trigger is-auto"
          :class="{ 'is-hollow': !last }"
          @click="cb"
        >
          {{ msg }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant'

export default {
  components: {
    [ Swipe.name ]: Swipe,
    [ SwipeItem.name ]: SwipeItem
  },
  props: {
    cb: {
      type: Function,
      default: null
    }
  },
  data () {
    return {
      index: 0,
      picList: []
    }
  },
  computed: {
    has () {
      return this.picList.length > 0
    },
    step () {
      return this.picList[this.index]
    },
    last () {
      return this.index === this.picList.length - 1
    },
    msg () {
      return this.last ? this.$t('btn.guide') : this.$t('btn.skipupper')
    }
  },
  methods: {
    onBannerChange (current) {
      this.index = current
    }
  },
  created () {
    this.$http.post('/api/app/pic/tutorials').then(data => {
      this.picList = data.picList.sort((a, b) => a.index - b.index)
    }, this.cb)
  }
}
</script>

<style lang="scss" scoped>
.c-guide {
  z-index: 999999;

  &__content {
    position: absolute;
    top: 88px + $save-header;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &__img {
    flex: 1 0 0;
    min-height: 0;
    padding: 0 120px;
  }

  &__text {
    flex: none;
    padding: 60px 75px 250px;
  }

  &__footer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 40px 75px;
  }

  &__button {
    padding: 0 100px;
  }

  /deep/ .van-swipe__indicator {
    background-color: $color-blue;
  }

  /deep/ .van-swipe__indicators {
    bottom: 180px;
  }
}
</style>
